




















export default {
		data() {
			return {
				// 用户输入的信息
				old_pass: '',
				new_pass: '',
				again_pass:''
			};
		},
		methods: {
			// 获取用户输入的数据
			getText(e, name) {
				var that = this;
				if (name == 'old_pass') {
					that.old_pass = e.detail.value;
				} else if (name == 'new_pass') {
					that.new_pass = e.detail.value;
				}else if(name == 'again_pass'){
					that.again_pass = e.detail.value;
				}
			},
			// 提交注册信息
			submit(){
				var that = this;
				if(that.old_pass.length<3){
					uni.showToast({
						title:'旧密码!',
						icon:'none'
					})
				}else if(that.new_pass == 0 ){
					uni.showToast({
						title:'新密码不能为空!',
						icon:'none'
					})			
				}else if(that.new_pass!=that.again_pass){
					uni.showToast({
						title:'两次密码不一致！',
						icon:'none'
					})
				}else{
					uni.request({
						url:that.http+'user/changeSafePass',
						header:that.header,
						data:{
							old_pass:that.old_pass,
							new_pass:that.new_pass,
							again_pass:that.again_pass,
							user_id:uni.getStorageSync('token')
						},
						method:'POST',
						success: (res) => {
							if(res.data.code==200){
								uni.showToast({
									title:'修改安全密码成功!',
									icon:'none'
								})
							}else{
								uni.showToast({
									title:res.data.data,
									icon:'none'
								})
							}
						},
						fail(){
							uni.showToast({
								title:'网络延迟，请刷新后重试！',
								icon:'none'
							})
						}
					})
				}			
			}
		},
	}
